            <th class="table-title-item" width="70" style="flex-grow:2">ID</th>
            <th class="table-title-item" width="70" style="flex-grow:3">æµç§°</th>
            <th class="table-title-item" width="70" style="flex-grow:2">æ§å«</th>
            <th class="table-title-item" width="70" style="flex-grow:2">ç©å®¶ç±»å</th>
            <th class="table-title-item" width="70" style="flex-grow:2">ä¸çº§ID</th>
            <th class="table-title-item" width="70" style="flex-grow:2">ä¸çº§æµç§°</th>
            <th class="table-title-item" width="70" style="flex-grow:2">ä¸çº§ç±»å</th>
            <th class="table-title-item" width="70" style="flex-grow:8"></th>
          </tr>
          <tr v-for="(item, index) in currentApy" class="table-row table-body">
            <td class="table-body-item" width="25"  style="flex-grow:1">
