            <td class="table-body-item" width="70"  style="flex-grow:3">{{item.szNickName}}</td>
            <td class="table-body-item" width="70"  style="flex-grow:2">{{getGenderStr(item.cbGender)}}</td>
            <td class="table-body-item" width="70"  style="flex-grow:2">{{getTypeName(item.cbMemberType)}}</td>
            <td class="table-body-item" width="70"  style="flex-grow:2">{{item.dwParentID}}</td>
            <td class="table-body-item" width="70"  style="flex-grow:2">{{item.wParentNickName}}</td>
            <td class="table-body-item" width="70"  style="flex-grow:2">{{item.wParentType}}</td>

            <td class="table-body-item table-item-operate" width="70"  style="flex-grow:8">
