          </div>
          <div class="add-type">
            <span class="txt">æ·»å ç±»å:</span>
            <div class="button">
              <toggle-button :select="addType == 0" :index="0" @toggle="onSelectType"></toggle-button><span>ä¼å</span>
              <toggle-button :select="addType == 2" :index="2" @toggle="onSelectType" class="right-button"></toggle-button><span>åä¼äºº</span>
            </div>
          </div>
          <div class="add-num" v-show="addType == 2">
            <span class="txt">ä¸çº§ä¿¡æ¯:</span>
            <select v-model="dwParentGameID">
              <option :value="0" disabled selected hidden class="tip">æµç§°/ID</option>
              <option v-for="(item, index) in $store.getters.getTypePartner" :value="item.dwUserID">{{item.dwUserID}}/{{item.szNickName}}</option>

            </select>
          </div>
          <div class="btn-close">
            <my-button :commonSrc="agreeSrc" class="agree-btn" eventName="agree-event" @agree-event="addConfig"></my-button>
          </div>
        </div>


      </el-dialog>
      <accompony :dialogVisible="accomponyDialog" @closeDialog="closeDialog" ref="accomponyDialog"></accompony>

      <div class="father-wrapper">
        <!--ç¼è¾æ§å«çé¢-->
        <el-dialog
          title="åºæ¬ä¿¡æ¯"
          :visible.sync="baseInfo"
          width="30%"
          class="info-wrapper"
          top="30vh"
        >
          <div class="line"></div>
          <div class="content-wrapper">
            <div class="base-info">
              <div class="avatar">avatar</div>
              <div class="info">
                <div class="number">
                  <span class="txt">åºå·: </span><span class="index">{{getCurrentRobotIndex}}</span>
                  <span class="txt">ID: </span><span class="id">{{getCurrentRobotID}}</span>
                </div>
                <div class="nick-name">
                  <span class="txt">æµç§°:</span>
                  <input type="text" class="name" readonly="readonly" v-model="name"/>
                </div>
