                  <div class="sex">
                    <span class="txt">æ§å«:</span>
                    <div class="sex-radio">
                      <label><input type="radio" name="sex" :value="1" v-model="currentRobotGender"/> ç·</label>
                      <label><input type="radio" name="sex" :value="2" v-model="currentRobotGender"/> å¥³</label>
                    </div>
                  </div>
                </div>
              </div>
            </div>

            <div class="btn-close">
              <my-button :commonSrc="agreeSrc" class="agree-btn" eventName="agree-event" @agree-event="_configRobotModify"></my-button>
            </div>
          </div>


