</template>

<script>
  import MyButton from '../myButton/MyButton.vue';
  import NavFooter from '../navFooter/NavFooter.vue';
  import ToggleButton from '../myToggleButton/MyToggleButton.vue';
  import {mapGetters} from 'vuex';

  //éªç©é¡µé¢
  import Accompony from '../accompony/Accompony.vue';

  import {FrameCtrl} from '../../frame/FrameCtrl';


  export default {
    data() {
      return {
        addSrc: "btn_add",
        editSrc: "btn_edit",
        deleteSrc: "btn_delete",
        deleteAllSrc: "btn_delete_all",
        agreeSrc: "btn_agree",
        infoTip: "å¨é",
        btnCancel: false,
        addApy: false,
        baseInfo: false,
        addCount: 1,
        addType: 0,
        dwParentGameID: 0,
        accomponyDialog: false,
        name: "ç±çé­åè½¬åå",
        //å½åæ­£å¨ç¼è¾æºå¨äººçæ°æ®
        currentEditRobot: null,
        currentRobotGender: 1
      }
    },
    created() {

    },
    mounted() {
      //å¦ææ²¡æstoreï¼nav - 0

    },
    computed: {
      ...mapGetters(['currentApy', 'totalGroupIndex', 'getGroupById', 'getTypePartner']),
      getCurrentRobotIndex() {
        if (this.currentEditRobot) {
          return this.currentEditRobot.index;
        } else {
          return "æªè·åå°index"
        }
      },
      getCurrentRobotID() {
        if (this.currentEditRobot) {
          return this.currentEditRobot.dwUserID;
        } else {
          return "æªè·åå°ID"
        }
      }
    },
    methods: {
      getTypeName(kind) {
        switch (kind) {
          case 0: {
            return "ä¼å";
          }
          case 2: {
            return "åä¼äºº";
          }
        }
      },
      getGroupName(id) {
        if (id === undefined) return "";
        if (this.getGroupById(id)) {
          return this.getGroupById(id).szGroupName;
        } else {
          return "æªè·åå°ä¿±ä¹é¨åç§°";
        }
      },
      toggleBtn() {},
      editRobot(index) {
        //æå¼ç¼è¾é¡µé¢
        console.log("ç¼è¾é¡µé¢:", index);
        let robot = this.currentApy[index];
        console.log(robot);
        this.currentEditRobot = robot;
        this.name = robot.szNickName;
        this.currentRobotGender = robot.cbGender === 0 ? 1 : robot.cbGender;
        this.baseInfo = true;
      },
      /**
       * ç¡®å®æºå¨äººä¿®æ¹
       * */
      _configRobotModify() {
        let robot= this.currentEditRobot;
        let [groupID, userID, gender] = [robot.dwGroupID, robot.dwUserID, this.currentRobotGender];
        console.log(robot, gender);
        if (gender === robot.cbGender) return;
        if (this.$store.state.debug === false ) {
          console.log(robot);
          console.log(robot.dwGroupID, robot.dwUserID, 1);
          FrameCtrl.getIns().modifyAndroid(groupID, userID, gender);
        } else {
          //this.
          let newRobot = {};
          for(let key in robot) {
            newRobot[key] = robot[key];
          }
          newRobot.cbGender = gender;
          this.$store.commit('pushAcpAry', newRobot);
        }
        this.baseInfo = false;
      },
      /**
       * å é¤åä¸ªæºå¨äºº
       * @param index: å½åçæ°ç»çindex
       * */
      deleteRobot(index) {
        this.$confirm("ç¡®å®å é¤è¯¥æºå¨äºº?").then(() => {
          let deleteId = this.currentApy[index].dwUserID;
          let groupId = this.currentApy[index].dwGroupID;
          if (deleteId === null) return;
          if (this.$store.state.debug === true) {
            console.log('æåå é¤', deleteId);
            this.$store.commit('deleteAcpAry', {
              userID: deleteId,
              groupID: groupId
            })
          } else {
            FrameCtrl.getIns().deleteRobot(deleteId, groupId);
          }
        }).catch(err => {
            console.warn("å é¤æºå¨äººå¤±è´¥:", err);
        })
      },
      /**
       * å é¤ææå·²éæºå¨äºº
       * */
      deleteSelectedRobots() {
        this.$confirm("ç¡®å®å é¤ææå·²éæºå¨äºº?").then(() => {
          //ç¡®å®å é¤
          let groupID = this.$store.state.robot.dwGroupID;
          let robotsID = [];
          this.$store.state.accompany.acyAry.forEach(robot => {
            if (robot.dwGroupID !== groupID) {
              console.warn("ä¸æ­£ç¡®çç¾¤ç»ID", robot.dwGroupID, groupID);
            }
            if (robot.isSelect === true && robot.dwGroupID === groupID) {
              robotsID.push(robot.dwUserID);
            }
          });

          robotsID.forEach(id => {
            if (this.$store.state.debug === true) {
              this.$store.commit('deleteAcpAry', {
                dwGroupID: groupID,
                dwUserID: id
              })
            } else {
              FrameCtrl.getIns().deleteRobot(id, groupID);
            }

          })

        }).catch(err => {
          //åæ¶å é¤
          console.log("åæ¶å é¤");
        })
      },
      _cancel(index) {
        console.log("éä¸­:", index);
        this.btnCancel = index;
        this.$store.commit('allAcp', index);

      },
      /**
       * æ·»å æºå¨äºº
       * */
      addConfig() {
        let err = false;
        let msg ="";
        if (this.addCount < 0) {
          err = true;
          msg = "è¯·è¾å¥æ­£ç¡®çæ°å­";
        }
//        if (this.addType === 2 && this.dwParentGameID === 0) {
//          err = true;
//          msg = "è¯·éæ©ä¸çº§";
//        }
        if (err) {
          this.$alert(msg, "éè¯¯åå :", {
            confirmButtonText: 'ç¡®å®',
          }).catch(err => {
            console.log(err);
          });
          return;
        }
        if (this.$store.state.debug === true) {
            console.log("æ·»å æºå¨äºº");
        } else {
          let id = this.$store.state.robot.dwGroupID;
          let parentID = this.dwParentGameID;
          let type = this.addType;
          let count = parseInt(this.addCount);
          console.log("æ·»å æºå¨:", id, type, parentID, count);
          if (id === undefined || id === null) {
            console.log("æ·»å æºå¨GroupIDåºé", id);
            return
          }

          FrameCtrl.getIns().appendAndroid(id, type, parentID, count);
        }
        this._addApy();
      },
      _addApy() {
        let id = this.getGroupById(this.$store.state.robot.dwGroupID);
        if (id == null) {
          this.$confirm("è¯·åéæ©éè¦ç¼è¾çä¿±ä¹é¨.", "éè¯¯", () => {

          }).catch(err => {
            console.log(err);
          });
          return
        }
        this.addApy = !this.addApy;
      },
      _close() {

      },
      onSelectType(obj) {
        this.addType = obj.type;
      },
      _onChangePages(index) {
        this.$store.commit("changeAcpIndex", index);
      },
      toggleBtn(event) {
        let index = event.type;
        //å¦ä½æ¾å°stateå¹¶ä¸æ¹åisSelect
        //å¦ä½æ ¹æ®type
        let target = this.currentApy[index];
        this.$store.commit('toggleAcp', target);
      },
      onTogglePage(index) {
        this._closeAllDialog();
        let dialog = this._getDialog(index);
        dialog.openDialog();
      },
      closeDialog(index) {
        console.log("å³é­é¡µé¢:", index);
        let dialog = this._getDialog(index);
        dialog.closeDialog();
      },
      _getDialog(index) {
        let dialog;
        switch (index) {
          case 1 : {
            dialog = this.$refs.accomponyDialog;
            break;
          }
          case 2 : {
//            dialog = this.$refs.modifyDialog;
            break;
          }
          case 3 : {
//            dialog = this.$refs.putInDialog;
            break;
          }
        }
        return dialog;
      },
      _closeAllDialog() {
        for (let n = 1; n <= 2; n++ ) {
          this.closeDialog(n);
        }
      },
      getGenderStr(gender) {
        switch (gender) {
          case 1: {
            return "ç·";
          }
          case 2: {
            return "å¥³";
          }
          default: {
            return "æªç¥";
          }

        }
      },
      clearNoNum(e) {
        e.target.value = e.target.value.replace(/[^\d]/g,'');
        console.log(e.target.value, typeof e.target.value);
        console.log(parseInt(e.target.value));
        e.target.value = parseInt(e.target.value);
        if (parseInt(e.target.value) < 0) {
          e.target.value = 0;
        }
      },
    },

    components: {
      'my-button': MyButton,
      'nav-footer': NavFooter,
      'toggle-button': ToggleButton,
      'accompony': Accompony
    }
  }
</script>

<style   scoped type="text/stylus" lang="stylus">
  @import "./stylus/serverMag.styl";
</style>



