export function alloc3Array(dimension, count, count1, instance) {
    var arr = [];
    for (var i = 0; i < dimension; i++) {
        arr[i] = [];
        for (var j = 0; j < count; j++) {
            arr[i][j] = [];
            for (var k = 0; k < count1; k++) {
                arr[i][j][k] = createInstance(instance);
            }
        }
    }
    return arr;
    // }
}



