export default function writeAuthCode(options) {
  let canvas = document.getElementById(options.canvasId);
  canvas.width=options.width||300;
  canvas.height=options.height||150;
  let ctx = canvas.getContext('2d');/**åå»ºä¸ä¸ªcanvaså¯¹è±¡*/
  ctx.textBaseline = "middle";
  ctx.fillStyle = randomColor(180, 255);/**è¿ä¸ªèå´çé¢è²ä½èæ¯çèµ·æ¥æ¸æ°ä¸äº*/
  ctx.fillRect(0, 0, options.width, options.height);
  for (let i = 0; i < options.txt.length; i++) {
    let txt = options.txt.charAt(i);/**è®©æ¯ä¸ªå­ä¸ä¸æ ·*/
    ctx.font = '20px SimHei';
    ctx.fillStyle = randomColor(50, 160); /**éæºçæå­ä½é¢è²*/
    ctx.shadowOffsetY = randomNum(-3, 3);
    ctx.shadowBlur = randomNum(-3, 3);
    ctx.shadowColor = "rgba(0, 0, 0, 0.3)";
    let x = options.width / (options.txt.length+1) * (i+1);
    let y = options.height / 2;
    let deg = randomNum(-30, 30);
    /**è®¾ç½®æè½¬è§åº¦ååæ åç¹*/
    ctx.translate(x, y);
    ctx.rotate(deg * Math.PI / 180);
    ctx.fillText(txt, 0, 0);
    /**æ¢å¤æè½¬è§åº¦ååæ åç¹*/
    ctx.rotate(-deg * Math.PI / 180);
    ctx.translate(-x, -y);
  }
  /**1~4æ¡éæºå¹²æ°çº¿éæºåºç°*/
  for (let i = 0; i < randomNum(1,4); i++) {
    ctx.strokeStyle = randomColor(40, 180);
    ctx.beginPath();
    ctx.moveTo(randomNum(0, options.width), randomNum(0, options.height));
    ctx.lineTo(randomNum(0, options.width), randomNum(0, options.height));
    ctx.stroke();
  }
  /**ç»å¶å¹²æ°ç¹*/
  for (let i = 0; i < options.width / 6; i++) {
    ctx.fillStyle = randomColor(0, 255);
    ctx.beginPath();
    ctx.arc(randomNum(0, options.width), randomNum(0, options.height), 1, 0, 2 * Math.PI);
    ctx.fill();
  }
};
/**éæºæ°å­**/
function randomNum(min, max) {
  return Math.floor(Math.random() * (max - min) + min);
}
/**éæºé¢è²**/
function randomColor(min, max) {
  let r = randomNum(min, max);
  let g = randomNum(min, max);
  let b = randomNum(min, max);
  return "rgb(" + r + "," + g + "," + b + ")";
}



