const ruleStore = {
  state: {
    ruleAry: [],
    ruleCurrentIndex: 1
  },
  mutations: {
    pushRuleAry(state, payload) {
      //å¯ä»¥éªè¯æ°æ®ç»æ;

      state.ruleAry = payload;
    },
    addRule(state, payload) {
      state.ruleAry.push(payload);

      //å¤æ­æ¯æ°å¢è¿æ¯ä¿®æ¹
      let ruleIndex = -1;
      state.ruleAry.forEach((item, index) => {
        if (item.dwTemplateID === payload.dwTemplateID) {
          ruleIndex = index;
        }
      });
      if (ruleIndex === -1) {
        payload.isSelect = false;
        state.ruleAry.push(payload);
      } else {
        // state.niuAry.splice(niuIndex)
        payload.isSelect = false;
        state.ruleAry.splice(ruleIndex, 1);
        state.ruleAry.splice(ruleIndex, 0, payload);
        console.error("ruleä¿®æ¹åçæ°æ®ç»æ:", payload);
      }

    },
    deleteRuleModule(state, payload) {
      let deleteIndex = -1;
      state.ruleAry.forEach((niu, index) => {
        if (niu.dwTemplateID === payload.dwTemplateID) {
          deleteIndex = index;
        }
      });
      if (deleteIndex !== -1) {
        state.ruleAry.splice(deleteIndex, 1);
      } else {
        console.log("æªæ¾å°éè¦å é¤çå åæ¨¡æ¿:", payload.dwTemplateID);
      }
    },
    changeRuleIndex(state, payload) {
      //åéªè¯ï¼å»é¤ä¸ç¬¦åè§åçé¡µé¢
      if (payload < 1 || payload > Math.ceil(state.ruleAry.length / 8)) {
        console.warn("éè¯¯çé¡µé¢:", payload);
        return
      }
      state.ruleCurrentIndex = payload;
    },
    toggleRule(state, payload) {
      console.warn(payload);
      let index = state.ruleAry.indexOf(payload);
      if (index >=0 && index < state.ruleAry.length) {
        state.ruleAry[index].isSelect = !state.ruleAry[index].isSelect;
      }
    },
    //å¨é
    allRule(state, payload) {
      state.ruleAry.forEach(acp => {
        acp.isSelect = payload;
      })
    },
    reverseRule(state, payload) {
      state.ruleAry.forEach(acp => {
        acp.isSelect = !acp.isSelect;
      })
    }
  },
  getters: {
    currentRule(state) {
      return state.ruleAry.slice((state.ruleCurrentIndex - 1) * 8, (state.ruleCurrentIndex) * 8);
    },
    totalRuleIndex(state) {
      return Math.ceil(state.ruleAry.length / 8);
    },
    getRuleName(state) {
      return id => {
        let module = state.ruleAry.find(item => item.dwTemplateID == id);
        if (module != null) {
          return module.szTemplateName;
        } else {
          return "æ ";
        }
      }
    },
    getRuleTemplateByID(state) {
      return id => {
        let module = state.ruleAry.find(item => item.dwTemplateID == id);
        return module;
      }
    }
   }
}

export {ruleStore};



